import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { routing } from './class.routing';
import { HttpModule } from '@angular/http';
import { NgaModule } from '../../theme/nga.module';
import { Ng2SmartTableModule } from 'ng2-smart-table';

import { ClassComponent } from './class.component';
import { ClassManageComponent } from './class-manage.component';
import { ClassService } from './class.service';

import { UserService } from '../user/user.service';

// import { SearchFilterPipe } from 'app/services/search-filter.pipe';
import { PaginationService } from '../../services/pagination.service';
import { MultiSelectModule } from '../../components/multiselect/multiselect.component';

const classComponentList = [ClassComponent, ClassManageComponent];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    routing,
    NgaModule,
    HttpModule,
    Ng2SmartTableModule,
    MultiSelectModule,
  ],
  declarations: [ClassComponent, ClassManageComponent],
  providers: [ClassService, PaginationService, UserService],
})

export class ClassModule {


 }



