import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { ClassService } from './class.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/takeWhile';
import { CommonProperties } from '../common-properties';

import { PaginationService } from '../../services/pagination.service';

@Component({
  selector: 'class-manage',
  templateUrl: './class-manage.component.html',
  // styleUrls: ['./smartTables.scss'],
   styleUrls: ['./class.component.css'],
  
})
export class ClassManageComponent implements OnInit, OnDestroy {

  isSubscribe: boolean = true;
 classServiceUrl: string;
  title = 'Manage class';
  itemList: any;
  query: string = '';
  returnUrl: string;
  classID: number;

  constructor(private classService: ClassService, private route: ActivatedRoute, private router: Router, 
              private paginationService: PaginationService ) { }

  ngOnInit() {
    this.classServiceUrl = CommonProperties.serviceURL;
    this.getClass();
  }

  ngOnDestroy() {
    this.isSubscribe = false;
  }

  // New class
  newClass() {
    this.router.navigate(['class/class/']);
  }

// Get class
  getClass() {
    $('#popup').fadeIn();
    $('#fade').fadeIn();
          this.classService.getClasses().takeWhile(() => this.isSubscribe)
                          .subscribe(
                            resData => {
                            this.paginationService.itemList = resData;
                            $('#popup').fadeOut();
                            $('#fade').fadeOut();
                            },
                            err => console.error(err),
                            () => { 
                              this.paginationService.filteredItems = this.paginationService.itemList;
                              this.paginationService.init();
                           },
                      );
  }

  // Update class
  updateClass(id) {
    this.returnUrl = `class/class/${id}`;  
    this.router.navigate([this.returnUrl]);
  }

  setClassID(id): void {
   // console.log(id);
    this.classID = id;
  }
  // Delete class
  deleteClass(): void {
      this.classService.deleteClass(this.classID).takeWhile(() => this.isSubscribe).subscribe(
         data => {
           // refresh the list
           this.getClass();
           return true;
         },
         error => {
           console.error('Error deleting class!');
           return Observable.throw(error);
         },
      );
  }

  getFacultyName(faculties): string {
    let faculty: string = '';

  faculties.split('#').forEach(el => {
    faculty = `${faculty}, ${el.split(',')[0]}`;
  });
    return faculty.substring(1);
  }
  
}



