import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import { CommonProperties } from '../common-properties';
import { xhrHeaders } from '../xhr-headers';
import { Observable } from 'rxjs/Observable';
import { CommonService } from 'app/services/common.service';

@Injectable()
export class ClassService {

    private classServiceUrl = `${CommonProperties.serviceURL}api/classes/`; 
    // preferred to use template literal rather concatination  like below line
    // CommonProperties.serviceURL + 'api/admins/';  // URL to web api 

    constructor(private http: Http, private commonService: CommonService) {}

    getClasses(): Observable<any> {
        return this.http.get(this.classServiceUrl)
                        .map((res: Response) => res.json());
    }
   
    getClassByID(id) {
        return this.http.get(`${this.classServiceUrl}${id}`)
                .map((res: Response) => res.json());
    }
   
    saveClass(objclass) {
        const body = JSON.stringify(objclass);
        return this.http.post(this.classServiceUrl, body, xhrHeaders())
            .map((res: Response) => res.json());
    }

    deleteClass(id) {
        return this.http.delete(`${this.classServiceUrl}${id}`);
   }

  updateClass(id, objclass) {
      const body = JSON.stringify(objclass);
      return this.http.put(`${this.classServiceUrl}${id}`, body, xhrHeaders()).map((res: Response) => res.json());
  }
  
}



