import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import { CommonProperties } from '../common-properties';
import { xhrHeaders } from '../xhr-headers';
import { Observable } from 'rxjs/Observable';
import { CommonService } from 'app/services/common.service';

@Injectable()
export class UserService {

    private userServiceUrl = `${CommonProperties.serviceURL}api/admins/`; 

    // preferred to use template literal rather concatination  like below line
    // CommonProperties.serviceURL + 'api/admins/';  // URL to web api 

    constructor(private http: Http, private commonService: CommonService) {}

    getUsers(): Observable<any> {
        return this.http.get(this.userServiceUrl)
                        .map((res: Response) => res.json());
    }
   
    getUserByID(id) {
        return this.http.get(`${this.userServiceUrl}${id}`)
                .map((res: Response) => res.json());
    }

    getUserByRole(role: string) {
        return this.http.get(`${this.userServiceUrl}GetUserByRole/${role}`)
                .map((res: Response) => res.json());
    }

    saveUser(user) {
        const body = JSON.stringify(user);
        return this.http.post(this.userServiceUrl, body, xhrHeaders())
            .map((res: Response) => res.json());
    }

    deleteUser(id) {
        return this.http.delete(`${this.userServiceUrl}${id}`);
   }

  updateUser(id, user) {
      const body = JSON.stringify(user);
      return this.http.put(`${this.userServiceUrl}${id}`, body, xhrHeaders()).map((res: Response) => res.json());
  }

  uploadProfile(fileList, fileName) {
    if (fileList.length > 0) {  
        const file: File = fileList[0];  
        const formData: FormData = new FormData();  
        formData.append('uploadFile', file, fileName);  
       // this.commonService.uploadFile(formData);
        this.commonService.uploadFile(formData, 'User');
        }  
     }
}



