import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ClassService } from './class.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/takeWhile';
import { CommonProperties } from '../common-properties';

import { CommonService } from '../../services/common.service';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import { UserService } from '../user/user.service';


@Component({
  selector: 'class',
  templateUrl: './class.component.html',
  styleUrls: ['./class.component.css'],
})

export class ClassComponent implements OnInit, OnDestroy {

  isSubscribe: boolean = true;
  id: number;
  classSelected: any= {};
  classList: any;
  message: string;
  isErrorMessage: boolean= false;
  classname: string;
  objCurrentDate: Date;
  placeholder: string = 'Search Faculty';
 
  countries: any;

  actives: any = [
    { id: 0, name: 'False' },
    { id: 1, name: 'True' },
  ];
  
 faculties: any;

 returnUrl = `class/manageclass/`;  
 selectedItem: any;
 selectedFacultyID = [];
 selectedFaculty = [];

  constructor(private route: ActivatedRoute, private router: Router, private classService: ClassService, 
              private commonService: CommonService, private _http: Http, private userService: UserService, 
              private commonProperties: CommonProperties) { }

  ngOnInit() {
    // console.log(JSON.parse(localStorage.getItem('currentUser')));
    
    this.objCurrentDate = new Date();
    this.getFaculties();
    this.route.params.subscribe(
      params => {
             this.id = +params['id']; // (+) converts string 'id' to a number
      });
   // get the class to update
    if (this.id > 0) {
    this.getclassByID(this.id);
    }
  }
 
  ngOnDestroy() {
    this.isSubscribe = false;
  }

// for user|Faculty
getFaculties() {
  this.userService.getUserByRole('2').takeWhile(() => this.isSubscribe)
                      .subscribe(resData => {
                           this.faculties = resData;
                        //  console.log(this.faculties);
                      },
                      err => console.error(err),
                    );
}

// end for the same 

  // Get class
  getClass() {
          this.classService.getClasses().takeWhile(() => this.isSubscribe)
                          .subscribe(resData => this.classList = resData,
                            err => console.error(err),
                          // () => console.log(this.classList)
                          );
  }
                        
getclassByID(id) {
            this.classService.getClassByID(id).takeWhile(() => this.isSubscribe)
              .subscribe(resData => { 
                this.classSelected = resData[0]; 
                this.classSelected.IsActive = parseInt(this.classSelected.IsActive);
              //  console.log(resData[0]);
              //  console.log(this.classSelected);
                this.selectedFacultyID = this.classSelected.FacultyID.split(',');
                var faculty = this.classSelected.Faculty.split('#');
                for(var i=0; i< faculty.length; i++) {
                  this.selectedFaculty.push({ ID: parseInt(faculty[i].split(',')[0]),
                                              FirstName: faculty[i].split(',')[1]});
                }
              //  console.log(this.classSelected.FacultyID);
              },
                err => console.error(err),
      );
                            
}

  HideMessage() {
    this.message = '';
    // return this.isErrorMessage = !this.isErrorMessage;
  }

  // Update class
  updateclass(id, objClass) {
    objClass.ID = id;
    objClass.LastUpdatedBy = this.commonProperties.currentUser.ID; // JSON.parse(localStorage.getItem('currentUser')).ID;
    objClass.LastModifiedOn = new Date(); 
    objClass.CreatedBy = this.classSelected.CreatedBy;
    objClass.CreatedOn = this.classSelected.CreatedOn;
    objClass.FacultyID = this.selectedFacultyID.join();
    this.classService.updateClass(id, objClass).takeWhile(() => this.isSubscribe).subscribe (
       data => {
         // refresh the list
        // this.getclass();
         this.message = 'class updated successfully! ';
        // this.router.navigate([this.returnUrl]);
         return true;
       },
       error => {
         console.error('Error saving class!');
         return Observable.throw(error);
       },
    );

  }

SuperAd(isSuperAdmin) {
  // console.log(isSuperAdmin);
 // console.log(this.selectedItem);
  // alert(isSuperAdmin.value);
}

onBack() {
  this.router.navigate([this.returnUrl]);
}

selectFaculty(ID) {
  // console.log(this.classSelected.length);
  if(this.classSelected.length != undefined){
    this.classSelected.FacultyID.forEach(element => {
      if( ID == element) {
        console.log(element);
            return  parseInt(element);
        } else { return 0; }
    });
  }
}
 saveclass (objClass: any) {
  objClass.CreatedBy = this.commonProperties.currentUser.ID; // JSON.parse(localStorage.getItem('currentUser')).ID;
  objClass.CreatedOn = new Date(); 
  this.classService.saveClass(objClass).takeWhile(() => this.isSubscribe)
                    .subscribe(
                      resData => {
                        // this.getclass();
                        this.isErrorMessage = false;
                        this.classname = '';
                        this.message = 'Data saved successfully! ';
                      },
                      error => {
                        this.isErrorMessage = true;
                        this.message = `Error ${error}`;
                        console.error('Error saving class!');
                        return Observable.throw(error);
                  });
  }

  // Save class Record
  submitClass(value: any) {
    
    if (!value.form.valid || this.selectedFacultyID.length == 0) {
      this.message = 'Please enter required values!';
      this.isErrorMessage = true;
      this.classname = 'danger';
       return;
    }

// value.value.facultyID.forEach(el => {
//   // console.log(el);
//   selectedFaculty = `${selectedFaculty},${el}`;
// });

// value.value.facultyID = selectedFaculty.substr(1, selectedFaculty.length - 1);

  value.value.facultyID = this.selectedFacultyID.join();
    // update the class record
    if (this.id > 0) {
          this.updateclass(this.id, value.value);  
          value.form.reset();
    } else {
          this.saveclass(value.value);
          value.form.reset();
      }
  }

  onAutoSelectionChanged(item) {
    this.selectedItem = item;
    this.selectedFacultyID.push(item.ID);
   // console.log(this.selectedFacultyID.join());
  }

  onMultiSelectionRemove(item) {
    this.selectedFacultyID.splice(this.selectedFacultyID.indexOf(String(item.ID)), 1);
    // console.log(this.selectedFacultyID.join());
  }


}



