import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { Component, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { CommonService } from '../../services/common.service';
import { Http, Response, Headers, RequestOptions } from '@angular/http';


@Component({
  selector: 'multi-select',
  template: `
    <input id="multiSelect" type="text" placeholder={{placeholder}} class="form-control" [(ngModel)]=query (keyup)=filter()>
   
  <div class="suggestions" *ngIf="filteredList.length > 0">
      <ul *ngFor="let item of filteredList" >
          <li >
              <a (click)="select(item)">{{item.FirstName}}</a>
          </li>
      </ul>
  </div>
  <div *ngFor="let item of selected">
  <div class="selected" >
      <span>{{item.FirstName}}</span>
      <a (click)="remove(item)">x</a>
  </div>
</div>

`,
  styles: [`
  .suggestions {
    background-color: #222628;
    width: 100%;
    z-index: 9;
    margin: 0;
    max-height: 204px;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 5px 0px;
    position: relative;
    box-sizing: border-box;
    color: #8E8E8C;
    border: 1px solid #fff;
    border-radius: 7px;
}
.suggestions ul {
    padding-left:15px;

}
.suggestions ul li {
    display: list-item;
    list-style: outside none none;
    box-sizing: border-box;
    
}

.suggestions ul li a {
    float: left;
    padding: 6px 0;
    width:98% ;
}

 .selected {
    border:1px solid #ddd;
	float:left; 
	margin:2px;
    padding:5px 5px;
	cursor: pointer;
	position: relative;
   }
.selected a{
    margin-left: 5px;
	cursor:pointer;
    font-weight:bold;
    color:#cccccc;
}
a {
    cursor: pointer;
}

a:hover {
    opacity:0.7;
}
`],
})



export class MultiSelectComponent {

    elementRef: ElementRef;
     query = '';
        
    @Input()
    placeholder: string;

    @Input()
     listItems: any;

     @Output()
     onSelect = new EventEmitter<any>();

     @Output()
     onRemoveItem = new EventEmitter<any>();

     filteredList = [];

     @Input()
     selected = [];
    constructor(private myElement: ElementRef) { this.elementRef = myElement; }

  
    filter() {
        if (this.query !== '' && this.query.length > 1 ) {
            this.filteredList = this.listItems.filter(function(el) {
                return el.FirstName.toLowerCase().indexOf(this.query.toLowerCase()) > -1;
            }.bind(this));
        }else {
            this.filteredList = [];
        }
      }
      
      select(item) {
        this.selected.push(item);
       // this.query = item.FirstName;
        this.query = '';
        this.filteredList = [];
        this.onSelect.emit(item);
      }
      
      remove(item) {
        this.selected.splice(this.selected.indexOf(item), 1);
        this.onRemoveItem.emit(item);
      }

}


@NgModule({
    imports: [CommonModule, FormsModule],
    declarations: [MultiSelectComponent],
    exports: [MultiSelectComponent],
  })
export class MultiSelectModule {}


